import React, { useEffect,useState } from 'react';
import Flatpickr from 'react-flatpickr';
import 'flatpickr/dist/flatpickr.min.css';
import axios from 'axios'



const items1 = [" "]; //  
const items2 = [" "]; //  
const items3 = [" "]; //  

// majorCode 
function GetMajorCodeComponent({ onSelectMajorCode }) {
  const [resultList, setResultList] = useState<any[]>([]); //    
  const [selectedMajorCode, setSelectedMajorCode] = useState(""); //   majorCode  

  useEffect(() => {
    fetch("/codes/major-code", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      }
    })
      .then(response => response.json())
      .then(data => {
        console.log("Success:", data);
        setResultList(data.resultList); //   resultList  
      })
      .catch(error => {
        console.error("Error:", error);
      });
  }, []);

  const handleChange = (e) => {
    const selectedCode = e.target.value;
    setSelectedMajorCode(selectedCode); //    
    onSelectMajorCode(selectedCode); //    majorCode 
  };

  return (
    <select 
      className="flex-1 w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm mr-2"
      onChange={handleChange}
      value={selectedMajorCode}
    >
      <option value=""> </option>
      {resultList.map((item) => (
        <option key={item.majorCode} value={item.majorCode}>
          {item.majorName}
        </option>
      ))}
    </select>
  );
}

//subCode 
function GetSubCodeComponent({ majorCode, onSelectSubCode}) {
  const [resultList, setResultList] = useState<any[]>([]); //    
  const [loading, setLoading] = useState<boolean>(false); //   
  const [error, setError] = useState<string | null>(null); //   
  const [selectedSubCode, setSelectedSubCode] = useState("");

  interface SubCode {
    subCode: string;
    subName: string;
  }
  
  interface SubCodeResponse {
    resultList: SubCode[];
  }

  

  useEffect(() => {
    if (majorCode) {
      setLoading(true); //  
      fetch("/codes/api/waste-code", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ majorCode }), //  majorCode JSON   
      })
        .then(response => {
          if (!response.ok) {
            throw new Error('Network response was not ok');
          }
          return response.json();
        })
        .then(data => {
          console.log("Success:", data);
          if (data.resultList && Array.isArray(data.resultList)) {
            setResultList(data.resultList); //   resultList  
            
          } else {
            
            console.error("Unexpected data format:", data);
          }

          
        })
        
        .catch(error => {
          console.error("Error:", error);
          setError(error.message); //    
        })
        .finally(() => {
          setLoading(false); //  
        });
    }
  }, [majorCode]); // majorCode   useEffect 

  

  if (loading) {
    return <p>Loading...</p>;
  }

  if (error) {
    return <p>Error: {error}</p>;
  }

  const handleChange = (e) => {
    const selectedCode = e.target.value;
    setSelectedSubCode(selectedCode); //    
    onSelectSubCode(selectedCode); //    subCode 
  };

  return (
    <select
      className="flex-1 w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm mr-2"
      onChange={handleChange}
      value={selectedSubCode}
    >
      <option value=""> </option>
      {resultList.length > 0 ? (
        resultList.map((item) => (
          <option key={item.subCode} value={item.subCode}>
            {item.subName}
          </option>
        ))
      ) : (
        <option value="">No data available</option>
      )}
    </select>
  );
}

//DetailCode 
function GetDetailCodeComponent({ majorCode, subCode }) {
  const [resultList, setResultList] = useState<any[]>([]); //    
  const [loading, setLoading] = useState<boolean>(false); //   
  const [error, setError] = useState<string | null>(null); //   

  interface DetailCode{
    detailCode : string;
    nameCode : string;
  }

  interface DetailCodeResponse {
    resultList: DetailCode[];
  }

  useEffect(() => {
    if (majorCode && subCode ) {
      setLoading(true); //  
      fetch("/codes/api/waste-code", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ majorCode, subCode }), //  majorCode JSON   
      })
        .then(response => {
          if (!response.ok) {
            throw new Error('Network response was not ok');
          }
          return response.json();
        })
        .then(data => {
          console.log("Success:", data);
          if (data.resultList && Array.isArray(data.resultList)) {
            setResultList(data.resultList); //   resultList  
            
          } else {
            
            console.error("Unexpected data format:", data);
          }

          
        })
        
        .catch(error => {
          console.error("Error:", error);
          setError(error.message); //    
        })
        .finally(() => {
          setLoading(false); //  
        });
    }
  }, [majorCode,subCode]); // majorCode,subCode   useEffect 

  if (loading) {
    return <p>Loading...</p>;
  }

  if (error) {
    return <p>Error: {error}</p>;
  }

  

  return (
    <select
      className="flex-1 w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm mr-2"
    >
      <option value=""> </option>
      {resultList.length > 0 ? (
        resultList.map((item) => (
          <option key={item.detailCode} value={item.detailCode}>
            {item.nameCode}
          </option>
        ))
      ) : (
        <option value="">No data available</option>
      )}
    </select>
  );
}

//  
function ParentComponent() {
  const [selectedMajorCode, setSelectedMajorCode] = useState(""); //    majorCode 
  const [selectedSubCode, setSelectedSubCode] = useState("");

  const handleSelectMajorCode = (majorCode) => {
    setSelectedMajorCode(majorCode); // majorCode  
  };

  const handleSelectSubCode = (subCode) => {
    setSelectedSubCode(subCode); // majorCode  
  };

  return (
    <div className="flex">
      <GetMajorCodeComponent onSelectMajorCode={handleSelectMajorCode} />
      <GetSubCodeComponent majorCode={selectedMajorCode} onSelectSubCode = {handleSelectSubCode}/>
      <GetDetailCodeComponent majorCode={selectedMajorCode} subCode={selectedSubCode} />
    </div>
  );
}


